import dva from "dva";
import "./index.css";
import serviceWorker from "./tools/registerServiceWorker";
import Router from "./router";
import modal from "./model";
const app = dva();
console.log("9ea14fa147d97d628290f5f2ad89005f935d1c9f");
// devredux-logger
if (process.env.NODE_ENV === "development") {
  const { createLogger } = require("redux-logger");
  const logger = createLogger({ collapsed: true });
  app.use({ onAction: logger });
}

//  modal(reducers)
modal.forEach(m => {
  app.model(m);
});

// 
app.router(Router);

// App
app.start("#root");

serviceWorker();
